.intro {
  padding: 10px;
  display: flex;
  color: var( --light-text);
  height: 100vh;
  background-color: var(--intro-background);
}
.intro-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.intro-right {
  flex: 1;
  position: relative;
}
.intro-left-wrapper {
  padding: 40px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.intro-hello {
  font-size: 30px;
  font-weight: 300;
}
.intro-name {
  font-size: 60px;
}

.intro-title {
  height: 50px;
  overflow: hidden;
}

.intro-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}

.intro-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #dba093;
  display: flex;
  align-items: center;
}

.intro-img {
  margin-top: 25px;
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 768px) {
  .intro {
    flex-direction: column;
  }
  .intro-left-wrapper {
    padding: 10px;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100%;
  }

  .intro-text {
    display: none;
  }

  .intro-img {
    max-width: 50%;
  }

  .intro-title-item {
    justify-content: center;
  }
}

@media screen and (max-width: 480px) {
  .intro {
    flex-direction: column;
    margin-top: 50px;
  }

  .intro-left-wrapper {
    padding: 10px;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100%;
  }
  .intro-text {
    display: none;
  }

  .intro-img {
    max-width: 85%;
  }

  .intro-title-item {
    justify-content: center;
  }
}
