.header {
  display: flex;
  height: 100px;
  background-color: var(--navbar);
  font-size: 20px;
}

.header-name {
  font-weight: bolder;
  margin: 3rem;
}

.nav-link {
  color: var(--light-text) !important;
  margin: 2rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.navbar-nav :hover {
  transform: scale(1.05);
  box-shadow: 0 3px 3px 3px;
  border-radius: 0.2rem;
  transition: all 0.3s ease-in-out;
}

.navbar-nav :active {
  color: #dba093;
  box-shadow: 0 3px 3px 3px;
}

@media screen and (max-width: 768px) {
  .nav-link {
    margin: 0;
    padding: 0;
  }
}
@media screen and (max-width: 540px) {
  .header {
    font-size: 14px;
    align-items: center;
    justify-content: center;
  }
  .header-name {
    display: none;
  }
  .nav-link {
    margin: 0;
    padding: 0;
  }
}
@media screen and (max-width: 480px) {
  .header {
    font-size: 14px;
    align-items: center;
    justify-content: center;
  }
  .header-name {
    display: none;
  }
  .nav-link {
    margin: 0;
    padding: 0;
  }
}

@media screen and (max-width: 375px) {
  .header {
    font-size: 14px;
  }
  .header-name {
    display: none;
  }
}

@media screen and (max-width: 280px) {
  .header {
    font-size: 9px;
  }
  .header-name {
    display: none;
  }
}
